@charset "UTF-8";
.advantages__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  position: relative;
  counter-reset: advantages;
}
@media screen and (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.advantages__description-list {
  list-style-type: none;
  padding: 23px 18px 52px;
  margin: 0;
}
@media screen and (min-width: 768px) {
  .advantages__description-list {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
  }
}
@media screen and (min-width: 1200px) {
  .advantages__description-list {
    padding: 0 0 20px;
  }
}

.advantages__image {
  display: block;
}
@media screen and (min-width: 768px) {
  .advantages__image {
    width: 100%;
    object-fit: cover;
    min-height: 102%;
  }
}

.advantages__item:first-child::before {
  content: "";
  position: absolute;
  background-image: url("../../img/decorative/decorative-elements-welcome/intro-triangle-top.svg");
  width: 320px;
  height: 58px;
  background-repeat: no-repeat;
  background-color: #ffffff00;
  top: -11px;
  left: 0;
}
@media screen and (min-width: 321px) {
  .advantages__item:first-child::before {
    display: none;
  }
}
@media screen and (min-width: 768px) {
  .advantages__item:first-child::before {
    display: none;
  }
}

.advantages__item--colored-dark {
  background-color: #81b3d3;
}
@media screen and (min-width: 768px) {
  .advantages__item--colored-dark {
    display: flex;
    flex-wrap: wrap;
    grid-column: 1/-1;
  }
}

@media screen and (min-width: 768px) {
  .advantages__item--reverse {
    flex-direction: row-reverse;
    flex-wrap: nowrap;
  }
}

@media screen and (min-width: 768px) {
  .advantages__item--wrapper {
    display: flex;
  }
}
@media screen and (min-width: 1200px) {
  .advantages__item--wrapper {
    width: 100%;
  }
}

.button {
  font-family: inherit;
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;
  padding: 18px;
}

.button--colored {
  background-color: #81b3d3;
  max-width: 282px;
  box-sizing: border-box;
  border: none;
  text-align: center;
}
.button--colored:hover {
  background-color: #669ec0;
}
.button--colored:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
