.form__control-mark {
  position: absolute;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 50%;
}

.form__input-controls[type=radio]:checked + .form__control-mark::before {
  content: "";
  background-color: #000000;
  width: 16px;
  height: 16px;
  position: absolute;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.form__input-controls[type=checkbox]:checked + .form__control-mark::before {
  content: "";
  background-image: url("../img/decorative/tick-icon.svg");
  width: 24px;
  height: 18px;
  position: absolute;
  background-position: center;
  background-repeat: no-repeat;
  top: 14px;
  left: 50%;
  transform: translateX(-50%);
}

.form__input-controls:checked + .form__control-mark:active::before {
  opacity: 0.3;
}

